.banner{

    color: white;
    object-fit: contain;
    height: 448px;

}

.banner__contents{
    margin-left: 30px;
    
     padding-top: 140px; 
     height: 190px;
}
.banner__title{
    font-size: 3rem;
    font-weight: 800;
}
.banner__description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height:80px;
}
.banner__button{
    cursor: pointer;
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    padding-bottom: 0.5rem;
}
.banner__button:hover{
    color: black;
    background-color: white;
    transition: all 0.2s;
}

.banner--fadeBottom{
    height: 15.4rem;
    background-image: linear-gradient(
    180deg,
    transparent,
    rgb(17, 17, 2),
    #111

    );
}