.row__posters{
display: flex;
overflow-y: hidden;
   overflow-x: scroll;
   padding: 20px;
}
.row{
    margin-left: 20px;
    color: white;
}

.row__posters::-webkit-scrollbar{
    display: none;
}
.row__poster{
    object-fit: contain;
    max-height: 100px;
   width:100%;
   margin-right: 10px; 
   transition: transform 450ms;
   cursor: pointer;
}
.row__poster:hover{
    transform: scale(1.08);
}

.row__posterLarge{
    max-height: 250px;
}

.row__posterLarge:hover{
    transform: scale(1.18);
}